  line-height: var(--line);
  min-block-size: 100%;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
}

/* DEFAULT LINKS */
a:link, a:visited {
  display: inline-flex;
  color: currentColor;
  text-decoration: none;
  transition: var(--all-3);
}
a:hover, a:active {
  color: inherit;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  height: 100%;
  width: 100%;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

#root,
#__next {
  isolation: isolate;
}

/* CLICKABLE CARD */
.clickable-card:not(a) {
  position: static;
}

.clickable-card:not(a) a {
  position: static;
}

.clickable-card:not(a) > a:after {
  content: "";
  position: absolute;
  inset: 0;
  cursor: pointer;
  display: flex;
}

a.clickable-card {
  position: static;
}

a.clickable-card:after {
  content: "";
  position: absolute;
  inset: 0;
  cursor: pointer;
  display: flex;
}

/* OUTLINE */
:focus,
button:focus {
  outline-style: solid;
  outline-color: var(--alt);
  outline-inline-size: 0.2rem;
  outline-offset: 0.2em;
}

:focus:not(:focus-visible),
button:focus:not(:focus-visible),
input:active {
  outline: none;
}

/* SCREEN READER ACCESSIBLE NO-DISPLAY */
.screen-reader {
  position: absolute;
  inline-size: 1px;
  block-size: 1px;
  padding: 0;
  margin: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
}

/* USER ANIMATION PREFERENCE */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  ::before,
  ::after {
    animation-delay: -1ms;
    animation-duration: 1ms;
    animation-iteration-count: 1;
    background-attachment: initial;
    scroll-behavior: auto;
    transition-duration: 0s;
    transition-delay: 0s;
  }
}
